.backdrop-team {
    position: fixed;
    z-index: 100;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 300ms, visibility 300ms;
}

.no-scroll {
    overflow: hidden;
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.modal-team {
    position: absolute;
    top: 10px;
    background-image: 
        linear-gradient(rgba(58, 56, 47, 0.4), rgba(47, 48, 58, 0.4)),
        url("../images/team-photo/img-bg-team.jpg");
    height: 90%;    
    width: 320px;
    padding: 10px;
    @media screen and (min-width: 480px) {
        width:480px;
    }
    @media screen and (min-width: 768px) {
        width: 768px;
    }
    @media screen and (min-width: 1024px) {
        width: 1024px;
    }
    @media screen and (min-width: 1280px) {
        width: 1280px;
    }
}

.team-close-btn {
    display: block;
    position: absolute;
    height: 30px;
    width: 30px;
    text-align: center;
    margin-left: auto;
    margin-top: 32px;
    border: transparent;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    top: -20px;
    right: 8px;
    fill: #ffffff;
}

.team-close-btn:hover {
    background-color: #fafafa;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    fill: #000000;
}

.modal__icon--close {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.modal-team__title {
    color:#ffffff;
    text-align: center;
    margin-bottom: 50px;
    @media screen and (min-width: 1024px) {
        font-size: 30px;
        margin-bottom: 10px;
    }
    @media screen and (min-width: 1280px) {
        font-weight: 50px;
        margin-bottom: 133px;
    }   
}

.team-gallery-wrap {
    overflow-y: auto;
    height: 53%;
    max-width: 100%;
    @media screen and (min-width: 480px) {
        height: 79%;
    }
    @media screen and (min-width: 768px) {
        height: 79%;
    }
    @media screen and (min-width: 1024px) {
        height: 90%;
        overflow-y: hidden;
    }
    @media screen and (min-width: 1280px){
        height: 100%;
    }
}

.team-gallery {
    display: grid;
    max-width: 90%;
    grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
    grid-auto-rows: 245px;
    grid-gap: 10px;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    height: 70%;
    @media screen and (min-width: 768px) {
        height: 80%;
        width: 100%;
    }
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
        grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
        grid-auto-rows: 225px;
    }
}

::-webkit-scrollbar {
    background-color: transparent;
}

.team-card {
    padding: 5px;
    position: relative;
    box-shadow: 0px 1px 3px 0px black,
        0px 1px 1px 0px black, 0px 2px 1px -1px black;
    background-color: #ffffff;
}

.team-card:hover {
    transform: scale(1.03);
}

.team-photo-wrap {
    width: 160px;
    height: 170px;
    margin: 0 auto;
    margin-bottom: 10px;
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
        width: 130px;
        height: 140px;
    }
    
}

.team-photo {
    display: block;
    height: 100%;
    width: 100%;    
}

li {
    list-style: none;
}

.team-title-surname {
    display: block;
}

.team-title {
    font-weight: bold;
    font-size: 15px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #000000;
    text-align: center;
    margin-bottom: 5px;
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
        font-size: 13px;
        margin-bottom: 10px;
    }
}

.team-text {
    font-size: 15px;
    line-height: 1.2;
    color: #FF6B08;
    text-align: center;
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
        font-size: 13px;
    }
}